.title{
    display: flex;
    justify-content: center;
}
.image-grid{
    display:flex;
    flex-wrap:wrap;
    gap:20px;
    justify-content: center;
}
.image-card{
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #ccc;
    padding: 20px;
    box-shadow: 1px 2px 4px rgba(0,0,0,0.2);
    transition: box-shadow 0.3s ease-in-out;
    width: 300px;
}
.image-card:hover{
    box-shadow: 0 8px 12px rgba(0,0,0,0.2);  
}
.image-details{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 20px;
}
button{
    background-color: #f44336;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
}
p{
    display:flex;
    justify-content: center;
    font-size: 20px;
}